.left-panel {
    border-right: solid grey 1px;
    padding: 0px 80px 0 20px;
    display: flex;
    flex-direction: column;
}
.your-views{
    border-top: solid grey 1px; 
}
.tree-container{
    background: white;
    height: 100vh;
    padding-top: 20px;
    display: flex;
}
.page-heading {
    text-align: left;
    padding: 20px;
}
.directory {
    border-right: solid grey 1px;
    padding: 20px 80px 0 20px;
}
.directory-view ul {
    margin-left: 10px;
    padding-left: 20px;
    border-left: 1px dashed #ddd;
  }
  
  .directory-view li {
    list-style: none;
    color: #888;
    font-size: 17px;
    font-style: italic;
    font-weight: normal;
  }
  
  .directory-view a {
    border-bottom: 1px solid transparent;
    color: #888;
    text-decoration: none;
    transition: all 0.2s ease;
  }
  
  .directory-view a:hover {
    border-color: #eee;
    color: #000;
  }
  
  .directory-view .folder,
  .directory-view .folder > a {
    color: #777;
    font-weight: bold;
  }
  
  .directory-view li:before {
    margin-right: 10px;
    content: "";
    height: 20px;
    vertical-align: middle;
    width: 20px;
    background-repeat: no-repeat;
    display: inline-block;
    background-image: url("../table.png");
    background-position: center 2px;
    background-size: 60% auto;
  }
  
  .directory-view li.folder:before {
    background-image: url("../database.png");
    background-position: center top;
    background-size: 180% auto;
  }
.directory-view li.folder:before {
    background-image: url("../viewIcon.png");
    background-position: center top;
    background-size: 100% auto;
  }
  .data-view{
    display: flex;
    flex-direction: column;
  }
  .edit-view{
    width: 70vw;
    height: 55vh;
    border-bottom: solid grey 1px;
    margin-left: 20px;
  }
  table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  td, th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
  }
  
  tr:nth-child(even) {
    background-color: #dddddd;
  }
  .table-container{
    margin-top: 20px;
    margin-left: 20px;
  }
  .btn {
    background-color: DodgerBlue;
    border: none;
    color: white;
    padding: 12px 30px;
    cursor: pointer;
    font-size: 20px;
    border-radius: 4px;
    height: 45px;
  }

  .btn:hover {
    background-color: navy;
  }
  .table-header{
    display: flex;
    justify-content: space-between;
  }
  .button-container{
    display: flex;
    align-content: center;
    justify-content: space-between;
    width: 30vw;
  }
.editor-divide{
  display: flex;
}
.queryHeading {
  font: 600 15px/1.5 Helvetica, Verdana, sans-serif;
  padding-left: 20px;
}
.query-edit-ui{
  display: flex;
  width: 40vw;
}
.query-pad{
  width: 50%;
}